import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, Feriado } from '../../../../models/dtos/mfp/feriado';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const FeriadoSearchView: FC<SearchViewProps<Feriado>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: false,
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'dia',
      editable: false,
      sort: true,
      text: 'Dia',
    },
    {
      dataField: 'mes',
      editable: false,
      sort: true,
      text: 'Mês',
    },
    {
      dataField: 'ano',
      editable: false,
      sort: true,
      text: 'Ano',
    },
    {
      dataField: 'uf.nome',
      editable: false,
      sort: true,
      text: 'UF',
    },
    {
      dataField: 'cidade.nome',
      editable: false,
      sort: true,
      text: 'Cidade',
    },
  ];
  return (
    <WindowComponent
      title="Manutenção de Feriados"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'FeriadoEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default FeriadoSearchView;
