import { GlobalParameter } from '../models/dtos/ger/globalParameter';

export function hasEmpresaMFP(globalParameter: GlobalParameter): string | undefined {
  let result = null;
  if (!globalParameter.empresaMFP) {
    result =
      'A empresa selecionada não está configurada para o Módulo Folha de Pagamento. Para configurá-la acesse Manutenção > Configurações > Empresa';
  }
  return result;
}
